	module.exports = {"data":{"post":{"html":"<p>The Abendblatt had an article today on how <a title=\"\"Komm rein und finde raus\"\" href=\"http://www.abendblatt.de/daten/2003/09/18/209322.html\">English ad slogans are not understood by German consumers. Of the 12 slogans they listed, only 2 were understood by more the half the readers, and the median was 25%. A number of people thought they understood the slogans, but actually did not (the Douglas slogan 'Come in and find out' was incorrectly thought to mean 'Come in and find the exit'). The worst undersood slogans were Mitsubishi's 'Drive Alive' ('Survive the trip'), Siemens' 'Be inspired', and RWE's 'One Group. Multi Utilities', to which even a native English speaker can only say 'Huh?'</p>","fields":{"title":"Come In and Find The Exit","date":"18 Sep 2003","url":"https://papascott.de/2003-09-18-come-in-and-find-the-exit/"}}},"pathContext":{"slug":"/2003-09-18-come-in-and-find-the-exit/","prev":{"fields":{"title":"Bin Bisy Backson","date":"18 Sep 2003","slug":"/2003-09-18-bin-bisy-backson/"}},"next":{"fields":{"title":"Binary Security Updates for FreeBSD","date":"19 Sep 2003","slug":"/2003-09-19-binary-security-updates-for-freebsd/"}}}}

